/* styles the user card component (used on matches page) */

.userCardContainer {
  /* container of the user card */
  margin: auto;
  border-radius: 10px;
  border: 1px solid lightgrey;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 20px;
  text-align: left;
  margin-top: 60px;
  width: 70%;
}

.userAvatar {
  /* picture of user in card */
  border-right: 1px solid lightgrey;
  margin-top: 10px;
  height: 40px;
}

.userInfo {
  /* information of the user in the card */
  margin-top: 4px;
  margin-left: 10px;
}

.userCardText {
  /* text on the card */
  font-size: 0.8em;
}
#userName {
  /* only username on card */
  font-weight: bold;
  border-bottom: 1px solid lightgrey;
}



